<template>
  <div class="complex__type">
    <Enum v-if="'enum' in field"
          :enums="field.enum" />
    <ObjectType v-else-if="isObject"
                :title="$t('sub_property')"
                :field="field" />
    <ArrayType v-else-if="isArray"
               :type="'item' in field ? field.item : 'object'"
               :field="field[0]" />
    <!-- <ObjectType v-else-if="isArray"
                :title="$t('array_member') + '(object)' "
                :field="field.item" />
    <ObjectType v-else
                :title="$t('array_member') + `(${arrayType})`" /> -->
  </div>
</template>

<script>
import Enum from './Enum.vue';
import ObjectType from './ObjectType.vue';
import ArrayType from './ArrayType.vue';

export default {
  name: 'ComplexType',
  components: {
    Enum,
    ObjectType,
    ArrayType,
  },
  props: {
    field: [Object, Array],
  },
  computed: {
    isObject() {
      return this.field.$type === 'object';
    },
    isArray() {
      return Array.isArray(this.field);
    },
  },
};
</script>

<style lang="scss" scoped>
.complex__type {
  color: #444;
  border-radius: 5px;
  border: 1px solid #ddd;
}
</style>
