<template>
  <div class="type">
    <div class="title">
      {{title}}
    </div>
    <div class="body"
         v-if="!(isSimpleType || isArray)">
      <div class="field"
           v-for="(field,prop) in useableBody"
           :key="prop">
        <Field :field="field"
               :prop="prop" />
      </div>
    </div>
    <ArrayType v-else-if="isArray"
               :field="body[0]">
    </ArrayType>
    <div class="simple-body"
         v-else-if="isSimpleType">
      <span class="name">{{body.name}}</span>
      <span class="desc">{{body.desc}}123</span>
    </div>
    <div class="none"
         v-else>{{$t('none_body')}}</div>
  </div>
</template>

<script>
import Field from './ComplexType/Field.vue';
import ArrayType from './ComplexType/ArrayType.vue';

export default {
  name: 'simple-type',
  components: {
    ArrayType,
    Field,
  },
  props: {
    title: String,
    body: [Object, Array],
  },
  data() {
    return {
      publicProps: ['$name', '$desc', '$required', '$type', '$default'],
    };
  },
  computed: {
    useableBody() {
      const bd = {};
      if (typeof this.body === 'object' && this.body.$type === 'object') {
        Object.keys(this.body)
          .filter(key => !this.publicProps.includes(key))
          .forEach((key) => {
            bd[key] = this.body[key];
          });
      }
      return bd;
    },
    isArray() {
      return Array.isArray(this.body);
    },
    isSimpleType() {
      return typeof this.body === 'object' && typeof this.body.type === 'string';
    },
  },
};
</script>

<style lang="scss" scoped>
.type {
  .title {
    padding: 10px 0;
    font-weight: 600;
    font-size: 1.2rem;
    border-bottom: 2px solid #ddd;
  }

  .field {
    padding: 10px 0;
    border-bottom: 1px solid #eee;
  }

  .simple-body {
    margin-top: 10px;
    .desc {
      margin-left: 10px;
      color: #aaa;
    }
  }

  .none {
    margin: 10px 0;
  }
}
</style>
