<template>
  <div class="field__item">
    <div class="row">
      <span class="prop">{{prop}}</span>
      <span class="type">{{parentType}}</span>
      <span class="required"
            v-if="!getRequired">{{$t('optional')}}</span>
      <span class="name">{{field.desc || field.name || $t('none')}}</span>
      <el-popover placement="right"
                  trigger="hover"
                  v-if="otherProps">
        <div class="other_info">
          <div class="props__name">{{prop}}</div>
          <div class="props">
            <div class="props__item"
                 v-for="(value, otherKey) in otherProps"
                 :key="`${field.prop}_${otherKey}`">
              <span class="props__item__key">{{otherKey}} : </span>
              <span class="props__item__value"> {{value}}</span>
            </div>
          </div>
        </div>
        <span slot="reference"
              class="more">{{$t('more')}}
        </span>
      </el-popover>
    </div>
    <complex-type :field="field"
                  v-if="isComplexType" />
  </div>
</template>

<script>
import { Popover } from 'element-ui';
import ComplexType from './index.vue';
import { getParentType } from '../../../utils';

export default {
  name: 'field',
  components: {
    'el-popover': Popover,
    ComplexType,
  },
  props: {
    field: [Object, Array],
    prop: String,
  },
  data() {
    return {
      publicProps: ['name', 'desc', 'value', 'type', 'required', 'default'],
    };
  },
  computed: {
    isComplexType() {
      return Array.isArray(this.field)
        || '$type' in this.field
        || 'enum' in this.field;
    },
    getRequired() {
      return (typeof this.field === 'object' && 'required' in this.field) ? this.field.required : true;
    },
    parentType() {
      const field = this.field.type || this.field.$type || (Array.isArray(this.field) ? 'Array' : 'string');
      const parent = getParentType(field);
      return parent ? `${field} : ${parent}` : field;
    },
    otherProps() {
      const props = {};
      let count = 0;
      if (!this.isComplexType) {
        Object.keys(this.field).forEach((key) => {
          if (!this.publicProps.includes(key)) {
            count += 1;
            props[key] = this.field[key];
          }
        });
        return count > 0 ? props : null;
      }
      return null;
    },
  },
};
</script>

<style lang="scss" scoped>
.row {
  margin: 10px 0;

  .type {
    color: #aaa;
    margin-left: 10px;
  }

  .required,
  .more {
    border-radius: 4px;
    border: 1px solid #ccc;
    margin-left: 10px;
    color: #444;
    font-size: 0.9rem;
    padding: 2px;
  }

  .name {
    margin-left: 10px;
  }

  .more {
    cursor: pointer;
    background-color: #ccc;
    color: white;
  }
}
.props__name {
  font-weight: 600;
  font-size: 1.02rem;
}
</style>
