<template>
  <div>
    <div class="title">{{title}}</div>
    <template v-if="field">
      <div class="field"
           v-for="(field,prop) in useableFields"
           :key="prop">
        <Field :field="field"
               :prop="prop">
        </Field>
      </div>
    </template>
  </div>
</template>

<script>

export default {
  name: 'ObjectType',
  components: {
    Field: () => import('./Field.vue'),
  },
  props: {
    title: String,
    field: Object,
  },
  data() {
    return {
      publicProps: ['$name', '$desc', '$required', '$type', '$default'],
    };
  },
  computed: {
    useableFields() {
      const bd = {};
      Object.keys(this.field)
        .filter(key => !this.publicProps.includes(key))
        .forEach((key) => {
          bd[key] = this.field[key];
        });
      return bd;
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  padding: 10px;
  background-color: #eee;
  border-bottom: 1px solid #ddd;
  font-size: 0.9rem;
}

.field {
  padding: 0 10px;
  border-bottom: 1px solid #ddd;
  font-size: 0.85rem;

  &:last-child {
    border-bottom: none;
  }
}
</style>
