<template>
  <div class="api">
    <div class="row">
      <span v-if="api.auth"
            class="auth">
        {{$t('authorization')}}</span>
      <span class="name">{{api.name}}</span>
    </div>
    <div class="desc">{{api.desc}}</div>
    <div class="api__info">
      <Method :methods="api.method" />
      <span class="url">{{api.url}}</span>
    </div>
    <Type v-if="api.body"
          :title="$t('request_body') + getType('body')"
          :body="api.body" />
    <Code v-if="api.request_headers"
          :json="api.request_headers"
          :title="$t('request_headers')" />
    <Type v-if="api.query"
          :title="$t('request_body')"
          :body="api.query" />
    <Type v-else-if="api.url_param"
          :title="$t('request_route_params')"
          :body="api.url_param" />
    <Type :title="$t('response_body') + getType('response')"
          :body="api.response" />
    <div class="tests">
      <div class="test__item"
           v-for="(data,index) in api.test_data"
           :key="index">
        <Test :api="api"
              :data="data" />
      </div>
    </div>
  </div>
</template>

<script>
import Type from '../../components/API/SimpleType.vue';
import Test from '../../components/API/Test.vue';
import Method from '../../components/API/Method.vue';
import Code from '../../components/Code.vue';

export default {
  components: {
    Type, Test, Method, Code,
  },
  computed: {
    api() {
      return this.$store.state.api;
    },
  },
  methods: {
    getType(key) {
      if (key in this.api) {
        const body = this.api[key];
        if (Array.isArray(body)) {
          return ' (Array)';
        }
        if (typeof body === 'object') {
          if ('$type' in body && body.$type === 'object') {
            return '';
          } if ('type' in body && typeof body.type === 'string') {
            return ` (${body.type})`;
          }
        }
      }
      return '';
    },
  },
};
</script>

