  .row {
    display: flex;
    align-items: center;
  }

  .auth {
    color: white;
    background-color: #ff5d5d;
    padding: 2px 5px;
    border-radius: 5px;
    margin-right: 5px;
  }

  .name {
    font-size: 2rem;
  }

  .api__info {
    margin: 10px 0;
    padding: 15px 20px;
