<template>
  <div class="container">
    <div class="name">{{data.name}}</div>
    <div class="desc">{{data.desc}}</div>
    <div>
      <el-button type="primary"
                 :loading="loading"
                 @click="testApi">
        {{$t('test')}}
      </el-button>
      <span class="current__method">
        <Method :methods="methods"
                @change="currentMethod = $event" />
      </span>
      <pre class="url">{{url}}</pre>
    </div>
    <div class="info">
      <div class="info__title">
        {{$t('test_info')}}
      </div>
      <div class="info__body">
        <Code v-if="data.body"
              :json="data.body"
              :title="$t('test_request_body')">
        </Code>
        <Code v-if="api.response_headers"
              :json="api.response_headers"
              :title="$t('test_response_headers')" />
        <Code :json="data.response"
              :title="$t('test_response_body')">
        </Code>
      </div>
    </div>
    <div class="info"
         v-if="response"
         v-loading="loading">
      <div class="info__title">
        {{$t('response_info')}}
        <span class="status"
              :style="statusStyle">{{statusInfo}}</span>
      </div>
      <div class="info__body">
        <Code v-if="headers"
              :json="headers"
              :title="$t('response_headers')">
        </Code>
        <Code v-if="response"
              :json="response"
              :title="$t('response_body')">
        </Code>
      </div>
    </div>
  </div>
</template>

<script>
import { Message, Button } from 'element-ui';
import qs from 'qs';
import Method from './Method.vue';
import Code from '../Code.vue';
import createAPI, { defaultMethods } from '../../api';

export default {
  name: 'test',
  components: {
    Method,
    Code,
    'el-button': Button,
  },
  props: {
    api: Object,
    data: Object,
  },
  data() {
    return {
      status: {
        code: -1,
        text: '',
      },
      response: '',
      headers: '',
      loading: false,
      methods: defaultMethods,
      currentMethod: defaultMethods[0],
    };
  },
  watch: {
    api: {
      immediate: true,
      handler(api) {
        if (api) {
          this.handleMethod(api.method);
        }
      },
    },
    data: {
      immediate: true,
      handler(testData) {
        if (testData) {
          this.status.code = -1;
          this.status.text = '';
          this.response = '';
          this.headers = '';
          this.loading = false;
          this.handleMethod(testData.method);
        }
      },
    },
  },
  computed: {
    url() {
      const url = this.data.url || this.api.url;
      if (this.data.query) {
        const query = qs.stringify(this.data.query);
        return `${url}${query ? '?' : ''}${query}`;
      }
      return url;
    },
    statusInfo() {
      return `${this.status.code} ${this.status.text}`;
    },
    statusStyle() {
      const { code } = this.status;
      let color = '';
      if (code < 300) {
        color = '#49cc90';
      } else if (code >= 300 && code < 400) {
        color = '#fca130';
      } else {
        color = '#f93e3e';
      }
      return { backgroundColor: color };
    },
  },
  methods: {
    handleMethod(m) {
      if (m && !m.includes('*')) {
        this.methods = m.map(v => v.toUpperCase());
        const firstMethod = this.methods[0];
        this.currentMethod = firstMethod;
      }
    },
    selectMethod(method) {
      this.currentMethod = method;
    },
    getFormData() {
      const formData = new FormData();
      Object.keys(this.data.body).forEach((key) => {
        const value = this.data.body[key];
        const ext = key.split('.').pop();
        // 
        if (/(jp?eg|png)$/.exec(ext)) {
          const buffer = new ArrayBuffer(32);
          const file = new File(buffer, value, { type: `image/${ext}` });
          formData.append(key, file);
        } else {
          formData.append(key, value);
        }
      });
      return formData;
    },
    getBody() {
      return this.data.body || {};
    },
    testApi() {
      this.loading = true;
      const isFormData = this.api.body_mode === 'formdata';
      const requestHeaders = this.api.request_headers || {};
      const headers = {
        'Content-Type': isFormData ? 'multipart/form-data' : `application/${this.api.body_mode}`,
        ...requestHeaders,
      };
      createAPI({
        headers,
        url: this.url,
        method: this.currentMethod,
        body: isFormData ? this.getFormData() : this.getBody(),
      }).then((res) => {
        this.status.code = res.status;
        this.status.text = res.statusText;
        this.headers = res.headers;
        this.response = res.data;
      }).catch((err) => {
        const res = err.response;
        this.status.code = res.status;
        this.status.text = res.statusText;
        this.response = err.message;
        Message.error(err.message);
      }).finally(() => {
        this.loading = false;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  position: relative;

  .name {
    font-size: 1.2rem;
    font-weight: 600;
    margin: 5px 0;
  }

  .desc {
    font-size: 0.9rem;
    margin: 5px 0;
  }

  .url {
    display: inline-block;
    margin-left: 10px;
    font-size: 1.2rem;
  }

  .info {
    &__title {
      padding-left: 10px;
      margin: 5px 0;
      font-weight: 600;
      border-left: 5px solid #2372fa;
    }

    .status {
      color: white;
      font-size: 0.8rem;
      padding: 2px 8px;
      border-radius: 5px;
    }

    &__body {
      margin-left: 10px;
    }
  }

  .current__method {
    margin-left: 10px;
  }
  .none {
    margin: 10px 0;
  }
}
</style>
