<template>
  <div v-if="type !== 'object'">
    <div class="title">{{$t('array_member')}} ({{arrayType}})</div>
    <div class="name">{{field.name}}</div>
  </div>
  <ObjectType v-else
              :title="$t('array_member') + ' (object)'"
              :field="field">
  </ObjectType>
</template>

<script>
import ObjectType from './ObjectType.vue';
import { getParentType } from '../../../utils';

export default {
  name: 'ArrayType',
  components: {
    ObjectType,
  },
  props: {
    field: Object,
  },
  computed: {
    arrayType() {
      const parent = getParentType(this.type);
      return parent ? `${this.type} : ${parent}` : this.type;
    },
    type() {
      return this.field.$type || this.field.type;
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  padding: 10px;
  background-color: #eee;
  border-bottom: 1px solid #ddd;
}

.name {
  padding: 10px;
  font-size: 0.8rem;
}
</style>
